.about {
  padding-top: 60px;
}
.about__titles {
  text-align: center;
  margin-bottom: 54px;
}
.about__subtitle {
  margin-top: 0;
  margin-bottom: 12px;
  font-family: $primary-font;
  font-weight: 700;
  font-size: 18px;
  line-height: 1.22;
  text-transform: uppercase;

  color: $txt-primary-clr;
}
.about__title {
  @extend %title;
  margin-bottom: 0;

  font-family: $secondary-font;
  font-weight: 350;
  font-size: 30px;
  line-height: 1.27;
  letter-spacing: $ltr-spc;
  text-transform: uppercase;

  color: $txt-primary-clr;
}
.about__cow-picture {
  box-shadow: 0px 8px 30px rgb(153 114 23 / 25%),
    0 -22px 0 0 rgb(248 210 78 / 70%);
  // box-shadow: 1px -22px 7px 9px $shdw-clr;
  object-fit: cover;
  border-radius: 50%;
}
//   + 
.about__box-content {
  text-align: center;
}
.about__picture {
  margin-bottom: 40px;
}

.about__box {
  display: flex;
  flex-direction: column;
  gap: 20px;
}
.about__text-box {
  display: flex;
  padding: 0 50px;
  flex-direction: column;
  gap: 4px;
}
.about__text {
  @extend %description;

  font-family: $primary-font;
  font-size: $font-size-descr-mob;
  line-height: 1.75;
  letter-spacing: $ltr-spc;

  color: $txt-primary-clr;
  &:last-child {
    display: none;
  }
}
.about__button {
  @extend %button;
  &:hover {
    @extend %button--hover;
  }
  &:focus {
    @extend %button--focus;
  }
}

.about__button-icon {
  width: 12px;
  height: 12px;
  transform: translateY(30%);
}
//  480px - 767px
@media screen and (min-width: $tablet) {
  .about__titles {
    margin-bottom: 72px;
  }
  .about__subtitle {
    font-size: 22px;
  }
  .about__title {
    font-size: 34px;
  }
  .about__cow-picture {
    width: 330px;
    height: 330px;
  }
  .about__picture {
    margin-bottom: 0px;
  }
  .about__box-content {
    text-align: start;
    display: flex;
    align-items: center;
    justify-content: center;
    flex-direction: row;
    gap: 32px;
  }
  .about__text {
    font-size: $font-size-descr-tab;
    &:last-child {
      display: block;
    }
  }

  .about__text-box {
    padding: 0;
    gap: 8px;
  }
  .about__box {
    gap: 48px;
  }
}

//  1200px+
@media screen and (min-width: $desktop) {
  .about__titles {
    margin-bottom: 82px;
  }
  .about__subtitle {
    font-size: 30px;
  }
  .about__title {
    font-size: 36px;
  }
  .about__cow-picture {
    width: 490px;
    height: 490px;
  }
  .about__box-content {
    gap: 100px;
  }
  .about__text {
    font-size: $font-size-descr-desk;
  }
}
